.calmdown-default-theme {
  display: flex;
  width: 1024px;
  height: 768px;
  background-color: white;
  border: 1px solid #84A98C; }
  .calmdown-default-theme .cd-edit {
    flex: 1 1 50%;
    max-width: 50%;
    padding: 10px;
    border: 0;
    border-right: 1px solid #84A98C; }
    .calmdown-default-theme .cd-edit:focus {
      outline: 0; }
  .calmdown-default-theme .cd-preview {
    flex: 1 1 50%;
    max-width: 50%;
    padding: 10px;
    word-wrap: break-word;
    overflow-y: scroll;
    overflow-x: hidden; }
  .calmdown-default-theme .cd-html-input {
    display: none; }
